import './App.css'
import './Style.css'

// import Nav from './Components/Nav.js'
import Title from './Components/Title'
import Picture from './Components/Picture'
import Projects from './Components/Projects'
import ProjectsBtn from './Components/ProjectsBtn.js'
import AboutBtn from './Components/AboutBtn.js'
import CVBtn from './Components/CVBtn.js'
import ContactBtn from './Components/ContactBtn.js'

import {useSidebar} from './SidebarContext'
import {BrowserRouter as Router, Route, Switch, useLocation, Link} from 'react-router-dom'
// import {TransitionGroup, CSSTransition} from 'react-transition-group'

 
function App() {
  let sidebar = useSidebar(false)
  
  // const location = useLocation()

  return (
      <Router>
        <Switch>
          <div style={{position:"relative", display:'flex', width:'100vw', height:'100vh'}}>
            <div className={ sidebar ? 'navAfter' : 'navBefore' }>
                <Picture />
                <Title />
          
            <ul className="navigation">
              <li>
                <Link to='/my-portfolio/projects'>
                  <ProjectsBtn />
                </Link>
              </li>
          
              <li>
                <AboutBtn />
              </li>
          
              <li>
                <CVBtn />
              </li>
          
              <li>
                <ContactBtn />
              </li>
            </ul>
            </div>
            <div className = { sidebar ? 'contentAfter' : 'contentBefore' }>
              <Route exact path ='/my-portfolio/projects'component={Projects}/>
            </div>
          </div>
        </Switch>
      </Router>
  );
}

export default App;
